@import url(https://fonts.googleapis.com/css?family=Solway);
:root {
  --pink-orange: #FF414E;
  --primary-dark: #1D2228;
  --secondary-dark: #333333;
  --primary-yellow: #FACD3D;
}

body {
  margin: 0;
  font-family: 'Solway' !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button{
  background-color: #FACD3D;
  background-color: var(--primary-yellow);
  color: #1D2228;
  color: var(--primary-dark);
  font-weight: 600;
  border: none !important;
  outline: none !important;
  border-radius: 1.5em !important;
  padding: 0.5em 1.75em 0.5em 1.75em; 
  font-size: 1.25em !important;
}

button:focus {
  outline: none;
  box-shadow: none;
}
.AboutContainer{
    color: white;
    background: center / contain no-repeat fixed url(/static/media/aboutBackground.3e576e4e.png);
    height: 90%;
}

.Title{
    font-size: 1.8em;
}

.MainTitle{
    font-size: 2em;
    font-weight: 800;
    color: var(--primary-yellow);
}

.EducationText{
    font-style: italic;
    font-size: large;
}

.Qualification{
    font-weight: 800;
}

.ContentText{
    font-size: 1.1em;
}
.HeaderDivTitle{
    color: white;
    font-size: 1em;
    padding-left: 1em;
}

.MainDropDiv{
    overflow-x: hidden;
}

.DisplayNavigationMenuMainDiv{
    padding-bottom: 4em;
    z-index: 1070 !important;
    right: 0;
    margin-top: -5vh;
    margin-right: -10vw;
    float: right;
    border-top-left-radius: 50% ;
    border-bottom-left-radius: 50% ;
    border-bottom-right-radius: 50% ;
    background-color: var(--secondary-dark) ;
    overflow: hidden;
    position: relative;
}

.HideNavigationMenuMainDiv{
    right: 0;
    z-index: 1070 !important;
    margin-top: -5vh;
    margin-right: -10vw;
    float: right;
    overflow: hidden;
    position: relative;
}

.menuItem{
    font-size: 20px;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    padding-right: 5em;
    margin-right: 9vw;
    padding-left: 1em;
    color: white;
    transition: all 0.5s ease-in-out;
}

.menuItem:hover{
    background-color: var(--primary-yellow);
    color: var(--primary-dark);
    font-weight: 1000;
}

.menuButton{
    margin-top: 5vh;
    margin-right: 11vw;
    margin-bottom: 2vh;
    float: right !important;
    background: transparent;
    height: 3em;
    float: right;
    z-index: 1075 !important;
    opacity: 1 !important;
    padding-left: 2vw;
}


.LandingPageContainer{
    color: #F0F2F1;
    height: 100%;
}

.LandingPageImage{
    height: 70vh;
}

.Title{
    font-size: 3em !important;
    font-weight: 700;
}

.Intro{
    font-size: 1.75em !important;
    font-weight: 400;
}

.customUnderline{
    border-bottom: 2px solid #BFBFBF;
}

@media (max-width: 767.98px) { 
    .LandingPageImage{
        height: 45vh;
    }
    .Title{
        font-size: 25px !important;
    }
    .Intro{
        font-size: 18px !important;
    }
}
.RecognitionsContainer{
    overflow-x: hidden;
}

.RecognitionsContainer .Title{
    font-size: 2em;
    font-weight: 800;
    color: var(--primary-yellow);
}

.ImagesDiv {
    height: 70vh;
    overflow: hidden;
    position: relative;
}

.ImageDescription{
    z-index: 920 !important;
    position: absolute;
    font-size: 1.25em;
    font-weight: 700;
    background-color: rgba(250,205,61,0.7);
    text-align: center;
    color: black;
}

.SelectedImage{
    z-index: 900;
    height: auto;
    max-height: 65vh !important;
    max-width: 70vw !important;
    position: absolute;
    transition: all 1s ease-in-out;
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
}

.UnselectedImageRight{
    z-index: 800;
    height: auto;
    max-height: 50vh !important;
    max-width: 80vw !important;
    position: absolute;
    margin-left: 40vw;
    transition: all 1s ease-in-out;
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
}

.UnselectedImageLeft{
    z-index: 800;
    height: auto;
    max-height: 50vh !important;
    max-width: 80vw !important;
    position: absolute;
    margin-right: 40vw;
    transition: all 1s ease-in-out;
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
}

.ContainerTitle{
    font-size: 2em;
    font-weight: 800;
    color: var(--primary-yellow);
}

.SummaryCardDiv{
    color: white;
    margin-top: 2em;
    background-color: var(--primary-dark);
    padding: 1em;
    width: 17em;
    border-radius: 2em;
    font-size: 1.15em;
}

.WorkExpSummaryList{
    list-style-type: none;
    border-left: 3px solid var(--primary-yellow);
    padding: 0;
    margin-top: 5vh;
}

.WorkExpSummaryList  > li::before{
    content: '';
    background: var(--primary-yellow);
    display: inline-block;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    margin-left: -15px;
    margin-right: 15px;
    padding-bottom: 20px;
}

.SummaryCardDateRange{
    box-shadow:0.05em 0.05em 0.5em 0.05em white;
    float: left;
    border-radius: 2em;
    text-align: center;
}

.SummaryCardOrganization{
    font-weight: 800;
}

.SummaryCardLocation{
    font-style: italic;
}

.SummaryCardPosition{
    font-weight: 600;
}

.SummaryCardTextSpan{
    font-size: 0.8em !important;
}

.ExpModalContainer{
    width: 100%;
    height: 75vh;
    background-color: var(--primary-dark);
    color: white;
    border-radius: 1em;
    overflow-y: scroll;
    border-bottom: 1em;
    font-size: 1.25em; 
}

.ExpModalOrganizationDiv{
    border-top-right-radius: 1em;
    border-bottom-right-radius: 1em;
    padding: 1em 1em 1em 0;
    background-color: var(--primary-yellow);
    color: var(--primary-dark);
    font-weight: 800;
    margin-top: 0.5em;
}

.ExpModalOrganizationSpan{
    padding-left: 1em;
}

.ExpModalCloseButton{
    width: 2em;
    height: 2em;
    float: right;
    margin: 0.5em;
}

.ExpModalProjects-Name{
    font-weight: 700;
}

.ExpModalProjects-WorkExperience{
    font-weight: 400;
}

@media (max-width: 767.98px) { 
    .SummaryCardDiv{
        font-size: 1em;
    }
    .ExpModalContainer{
        font-size: 1em;
    }
}
.SkillSetContainer{
    background-color: var(--primary-dark);
    opacity: 0.5;
    overflow-x: hidden;
}

.SkillIcon{
    height: 3.5em;
    padding-left: 5vw;
    padding-right: 5vw;
    opacity: 1;
}

.SkillSetScroll{
    transition: all 1s ease-in-out;
}
.ContactPageContainer{
    color: white;
    height: 90%;
    background-color: var(--primary-dark);
    border-radius: 1em;
    margin: 0.5em;
    width: 100vw;
    max-width: 800px !important;
}

.Title{
    font-size: 1.8em;
}

.ContentImage{
    height: auto;
    max-width: 50vw;
}

.EmailDiv{
    margin-bottom: 1em;
    font-size: 1.25em;
}

.EmailText{
    font-size: 1.25em;
    font-weight: 800;
}

.SocialIcon{
    height: auto;
    width: 20vw;
    max-width: 55px;
    -webkit-filter: invert(1);
            filter: invert(1);
}
::-webkit-scrollbar {
  width: 0px;  /* Remove scrollbar space */
  background: transparent;  /* Optional: just make scrollbar invisible */
}

.headerBar{
  height: 10vh !important;
  border: 0;
}

.mainView {
  height: 80vh !important;
  overflow-y: scroll;
}

.skillSet-footer{
  height: 10vh !important;
  border: 0;
}

