.HeaderDivTitle{
    color: white;
    font-size: 1em;
    padding-left: 1em;
}

.MainDropDiv{
    overflow-x: hidden;
}

.DisplayNavigationMenuMainDiv{
    padding-bottom: 4em;
    z-index: 1070 !important;
    right: 0;
    margin-top: -5vh;
    margin-right: -10vw;
    float: right;
    border-top-left-radius: 50% ;
    border-bottom-left-radius: 50% ;
    border-bottom-right-radius: 50% ;
    background-color: var(--secondary-dark) ;
    overflow: hidden;
    position: relative;
}

.HideNavigationMenuMainDiv{
    right: 0;
    z-index: 1070 !important;
    margin-top: -5vh;
    margin-right: -10vw;
    float: right;
    overflow: hidden;
    position: relative;
}

.menuItem{
    font-size: 20px;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    padding-right: 5em;
    margin-right: 9vw;
    padding-left: 1em;
    color: white;
    transition: all 0.5s ease-in-out;
}

.menuItem:hover{
    background-color: var(--primary-yellow);
    color: var(--primary-dark);
    font-weight: 1000;
}

.menuButton{
    margin-top: 5vh;
    margin-right: 11vw;
    margin-bottom: 2vh;
    float: right !important;
    background: transparent;
    height: 3em;
    float: right;
    z-index: 1075 !important;
    opacity: 1 !important;
    padding-left: 2vw;
}
