.RecognitionsContainer{
    overflow-x: hidden;
}

.RecognitionsContainer .Title{
    font-size: 2em;
    font-weight: 800;
    color: var(--primary-yellow);
}

.ImagesDiv {
    height: 70vh;
    overflow: hidden;
    position: relative;
}

.ImageDescription{
    z-index: 920 !important;
    position: absolute;
    font-size: 1.25em;
    font-weight: 700;
    background-color: rgba(250,205,61,0.7);
    text-align: center;
    color: black;
}

.SelectedImage{
    z-index: 900;
    height: auto;
    max-height: 65vh !important;
    max-width: 70vw !important;
    position: absolute;
    transition: all 1s ease-in-out;
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
}

.UnselectedImageRight{
    z-index: 800;
    height: auto;
    max-height: 50vh !important;
    max-width: 80vw !important;
    position: absolute;
    margin-left: 40vw;
    transition: all 1s ease-in-out;
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
}

.UnselectedImageLeft{
    z-index: 800;
    height: auto;
    max-height: 50vh !important;
    max-width: 80vw !important;
    position: absolute;
    margin-right: 40vw;
    transition: all 1s ease-in-out;
    -webkit-transition: all 1s ease-in-out;
    -moz-transition: all 1s ease-in-out;
}
