
.LandingPageContainer{
    color: #F0F2F1;
    height: 100%;
}

.LandingPageImage{
    height: 70vh;
}

.Title{
    font-size: 3em !important;
    font-weight: 700;
}

.Intro{
    font-size: 1.75em !important;
    font-weight: 400;
}

.customUnderline{
    border-bottom: 2px solid #BFBFBF;
}

@media (max-width: 767.98px) { 
    .LandingPageImage{
        height: 45vh;
    }
    .Title{
        font-size: 25px !important;
    }
    .Intro{
        font-size: 18px !important;
    }
}