.ContainerTitle{
    font-size: 2em;
    font-weight: 800;
    color: var(--primary-yellow);
}

.SummaryCardDiv{
    color: white;
    margin-top: 2em;
    background-color: var(--primary-dark);
    padding: 1em;
    width: 17em;
    border-radius: 2em;
    font-size: 1.15em;
}

.WorkExpSummaryList{
    list-style-type: none;
    border-left: 3px solid var(--primary-yellow);
    padding: 0;
    margin-top: 5vh;
}

.WorkExpSummaryList  > li::before{
    content: '';
    background: var(--primary-yellow);
    display: inline-block;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    margin-left: -15px;
    margin-right: 15px;
    padding-bottom: 20px;
}

.SummaryCardDateRange{
    box-shadow:0.05em 0.05em 0.5em 0.05em white;
    float: left;
    border-radius: 2em;
    text-align: center;
}

.SummaryCardOrganization{
    font-weight: 800;
}

.SummaryCardLocation{
    font-style: italic;
}

.SummaryCardPosition{
    font-weight: 600;
}

.SummaryCardTextSpan{
    font-size: 0.8em !important;
}

.ExpModalContainer{
    width: 100%;
    height: 75vh;
    background-color: var(--primary-dark);
    color: white;
    border-radius: 1em;
    overflow-y: scroll;
    border-bottom: 1em;
    font-size: 1.25em; 
}

.ExpModalOrganizationDiv{
    border-top-right-radius: 1em;
    border-bottom-right-radius: 1em;
    padding: 1em 1em 1em 0;
    background-color: var(--primary-yellow);
    color: var(--primary-dark);
    font-weight: 800;
    margin-top: 0.5em;
}

.ExpModalOrganizationSpan{
    padding-left: 1em;
}

.ExpModalCloseButton{
    width: 2em;
    height: 2em;
    float: right;
    margin: 0.5em;
}

.ExpModalProjects-Name{
    font-weight: 700;
}

.ExpModalProjects-WorkExperience{
    font-weight: 400;
}

@media (max-width: 767.98px) { 
    .SummaryCardDiv{
        font-size: 1em;
    }
    .ExpModalContainer{
        font-size: 1em;
    }
}