.ContactPageContainer{
    color: white;
    height: 90%;
    background-color: var(--primary-dark);
    border-radius: 1em;
    margin: 0.5em;
    width: 100vw;
    max-width: 800px !important;
}

.Title{
    font-size: 1.8em;
}

.ContentImage{
    height: auto;
    max-width: 50vw;
}

.EmailDiv{
    margin-bottom: 1em;
    font-size: 1.25em;
}

.EmailText{
    font-size: 1.25em;
    font-weight: 800;
}

.SocialIcon{
    height: auto;
    width: 20vw;
    max-width: 55px;
    filter: invert(1);
}